<template>
  <div id="note" class="detail">
    <NoteSidebar @update:notes="val => notes = val"></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curNote.id"></div>
      <div class="note-detail-ct" v-show="curNote.id">
        <div class="note-bar">
          <span> : {{curNote.createdAtFriendly}}</span>
          <span> : {{curNote.updatedAtFriendly}}</span>
          <span> {{statusText}}</span>
          <span :class="{previewType:true,editor:isShowPreview,pre:!isShowPreview}">{{preview}}</span>
          <div class="iconWrapper">
            <span class="iconfont el-icon-delete" @click="onDeleteNote"></span>
            <i class="iconfont" :class="isShowPreview ? 'el-icon-edit':'el-icon-view'" @click="previewSwitch"></i>
          </div>

        </div>
        <div class="note-title">
          <input type="text" v-model:value="curNote.title" @input="onUpdateNote" @keydown="statusText='...'"
                 placeholder="">
        </div>
        <div class="editor">
          <textarea v-show="isShowPreview" v-model:value="curNote.content" @input="onUpdateNote"
                    @keydown="statusText='...'" placeholder=",  markdown "></textarea>
<!--          <codemirror v-show="isShowPreview" :options="cmOptions" v-model="curNote.content" @input="onUpdateNote" @inputRead="statusText='...'"-->
<!--                     placeholder=",  markdown "></codemirror>-->
          <div class="preview markdown-body" v-html="previewContent" v-show="!isShowPreview">
          </div>
        </div>
      </div>

    </div>
  </div>

</template>

<script>
  import authorise from '@/api/authorise.js';
  import NoteSidebar from "@/components/NoteSidebar.vue";
  import Bus from '@/support/bus.js'
  import _ from 'lodash'
  import notes from '@/api/notes.js'
  import MarkdownIt from 'markdown-it'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import { codemirror } from 'vue-codemirror'
  import 'codemirror/lib/codemirror.css'
  import 'codemirror/mode/markdown/markdown.js'
  import 'codemirror/theme/neat.css'

  let md = new MarkdownIt()

  export default {
    name: "NoteDetail",
    components: {
      NoteSidebar,
      codemirror
    },
    data() {
      return {
        // curNote: {},
        // notes: [],
        statusText: '',
        preview: '',
        isShowPreview: true,
        cmOptions:{
          tabSize:4,
          mode:'text/x-markdown',
          theme:'neat',
          lineNumbers:false,
          line: true,
        },
      }
    },
    computed: {
      previewContent() {
        return md.render(this.curNote.content || '')
      },
      ...mapGetters([
        'notes', 'curNote','curBook'
      ])
    },

    methods: {
      ...mapMutations([
        'setCurNote'
      ]),
      ...mapActions([
        'updateNote', 'deleteNote',
      ]),
      // deleteNote() {
      //   notes.deleteNote(this.curNote.id).then((res) => {
      //     this.notes.splice(this.notes.indexOf(this.curNote), 1)
      //     this.$message({
      //       type: 'success',
      //       message: res.msg
      //     });
      //     this.$router.replace({path: '/note'})
      //   }).catch((err) => {
      //     console.error(err)
      //   })
      // },
      onDeleteNote() {

          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.deleteNote({noteId: this.curNote.id}).then(()=>{
              this.setCurNote({})
              //0
              if(this.curBook.id&&this.curNote.id) {
                this.$router.replace({path: `/note?notebookId=${this.curBook.id}&noteId=${this.curNote.id}`})
              }
            })
            // this.$message({
            //   type: 'success',
            //   message: '!'
            // });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });


      },

      onUpdateNote: _.debounce(function () {
        if(!this.curNote.id){
          console.log('')
          return
        }
        this.updateNote({
          noteId: this.curNote.id, title: this.curNote.title, content: this.curNote.content,
        })
          this.statusText = ''

          , 300
      })
      ,
      previewSwitch() {
        console.log('',this.curNote.content)
        this.isShowPreview = !this.isShowPreview
        this.preview = this.isShowPreview ? '' : ''
      }
    }
    ,
    beforeRouteUpdate(to, from, next) {
      // console.log('to',to)
      // console.log('from',from)

      //NoteDetailsthis.notesnotesidebarthis.notes
      // this.curNotenotesidebarthis.notes
      // this.curNote = this.notes.find((notebook) => {
      //   // console.log(notebook, '---', to.query.noteId)
      //   return notebook.id === parseInt(to.query.noteId)
      // }) || {}
      this.isShowPreview = true
      this.setCurNote({curNoteId: to.query.noteId})

      next()
    },

    created() {
      // authorise.getInfo().then(res => {
      //   if (!res.isLogin && this.$router.history.current.path != '/login') {
      //     // this.$router.push({name: 'login'})
      //     this.$router.push({path: '/login'})
      //   }
      // })


      // Bus.$once('notes', (val) => {
      //   this.curNote = val.find((notebook) => {
      //     return notebook.id === parseInt(this.$route.query.noteId)
      //   }) || {}
      //   // console.log('notedetail',val)
      // })


    },
  }
</script>

<style lang='less' scoped>
  @import url(../assets/css/note-detail.less);

  #note {
    display: flex;
    align-items: stretch;
    background-color: #fff;
    flex: 1;
  }

</style>



